<template>
  <div>
    <!-- :visible.sync="dialogFormVisible" 控制显示跟隐藏 -->
    <el-dialog
      :title="isShow.edit ? '添加菜单' : '修改菜单'"
      :visible.sync="isShow.flag"
    >
      <!-- 通过 menu 组件传过来的 flag 进行 修改  -->

      <el-form ref="form" label-width="100px">


        <el-form-item label="菜单名称" prop="name">
          <el-input v-model="form.title"></el-input>
        </el-form-item>

        <el-form-item label="上级菜单">
          <el-select
            v-model="form.pid"
            placeholder="请选择"
            @change="changepid"
          >
            <!-- 循环便利 一下 下啦选项  -->
            <el-option label="顶级菜单" :value="0"></el-option>

            <el-option v-for="item in menuGetter"
              :key="item.id"
              :label="item.title"
              :value="item.id">

            </el-option>
          </el-select>
        </el-form-item>

        <!-- 这里 注意一下 如果使用 组 grounp 就在grounp中 加一个 v-model -->
        <el-form-item label="菜单类型">
          <el-radio v-model="form.type" :label="1" disabled>目录</el-radio>
          <el-radio v-model="form.type" :label="2" disabled>菜单</el-radio>
        </el-form-item>

        <!-- 
            目录：有图标，没有地址
            菜单：没有图标，有地址

            通过 v-if 来判断 
           -->

        <el-form-item
          v-if="form.type == 1 ? true : false"
          label="菜单图标"
          prop="region"
        >
          <!-- 循环遍历 icon 图标 -->
          <el-select v-model="form.icon" placeholder="请选择图标">
            <el-option
              v-for="item in icon"
              :key="item"
              :label="item"
              :value="item"
            >
              <i style="fontsize: 18px" :class="item"></i>
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item v-else label="菜单地址" prop="region">
          <el-select v-model="form.url" placeholder="请选择菜单地址">
            <el-option
              v-for="item in childrenRouter"
              :key="item.path"
              :label="item.name"
              :value="item.path"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="状态" prop="delivery">
          <!-- active-value="1" :inactive-value = "2" -->
          <!-- 设置 什么值打开 什么值关闭  -->
          <el-switch
            v-model="form.status"
            :active-value="1"
            :inactive-value="2"
          ></el-switch>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel()">取 消</el-button>
        <el-button type="primary" v-if="isShow.edit" @click="add()"
          >添 加</el-button
        >
        <!-- 修改按钮 通过 v-if控制  -->
        <el-button type="primary" @click="edit()" v-else>修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>


<script>
import { mapGetters, mapActions } from "vuex";
import { successAlert, warningAlert } from "../../../util/alert";
import { reqMenulist, reqOneEdite, reqEdit } from "../../../util/request";
import { childrenRouter } from "../../../router/index";

export default {
  computed: {
    ...mapGetters({
      menuGetter: "menu/menuGetter",
    }),
  },

  props: ["isShow"],
  methods: {
    ...mapActions({
      actionMenuList: "menu/actionMenuList",
    }),
    // 取消函数
    cancel() {
      this.form = {
        title: "",
        pid: 0,
        icon: "",
        type: 1,
        url: "",
        status: 1,
      };
      this.isShow.flag = false;
    },

    // 根据pid切换 下面选的 是目录还是 菜单
    changepid() {
      if (this.form.pid == 0) {
        this.form.type = 1;
      } else {
        this.form.type = 2;
      }
    },

    // 进行添加函数 发送请求 然后 判断状态码
    add() {
      reqMenulist(this.form).then((res) => {
        if (res.data.code == 200) {
          // 状态码正确进行 弹窗 清空 以及重新加载
          this.cancel();
          this.actionMenuList();

          successAlert(res.data.msg);
        } else {
          warningAlert(res.data.msg);
          this.cancel();
        }
      });
    },

    // // 挂载完成之后进行 调用触发
    // mounted() {
    //   this.actionMenuList();
    // },

    // 进行 获取某一条数据并添加进去
    getOne(id) {
      reqOneEdite(id).then((res) => {
        // 可以成功 获取要修改的对象
        // 进行 添加数据
        this.form = res.data.list;
        this.form.id = id;
      });
    },

    // 进行修改
    edit() {
      reqEdit(this.form).then((res) => {
        if (res.data.code == 200) {
          successAlert(res.data.msg);
          this.actionMenuList();
          this.cancel();
        } else {
          warningAlert(res.data.msg);
        }
      });
    },
  },

  data() {
    return {
      dialogFormVisible: true,
      form: {
        title: "",
        pid: 0,
        icon: "",
        type: 1,
        url: "",
        status: 1,
      },
      icon: [
        "el-icon-platform-eleme",
        "el-icon-eleme",
        "el-icon-user-solid",
        "el-icon-s-unfold",
      ],
      childrenRouter: childrenRouter,
    };
  },
};
</script>




